<template>
  <div class="orderManagement">
    <div class="navList">
      <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
        <van-tab v-for="(item,index) in orderClassifyList" :key="index" :title="item.label"></van-tab>
      </van-tabs>
      <div class="mid">
        <div class="list mescroll" id="list">
          <div class="list-data" id="listData">
            <div class="item" v-for="(item,index) in list" :key="index">
              <div class="item-top flex">
                <div class="No flex-2">订单编号:{{item.orderno}}</div>
                <div class="status" :style="item.orderStatus == 1 || item.orderStatus == 3?'color:#eb5252':item.orderStatus == 4?'color:#333':'color:#999'">{{$utils.getText(item.orderStatus)}}</div>
              </div>
              <div>
                <div class="item-goods flex">
                  <div class="goods-pic">
                    <img :src="item.aimg?$utils.dealImg(item.aimg):''" />
                  </div>
                  <div class="flex-2">
                    <p class="title">{{item.activityName?item.activityName:''}}</p>
                    <div class="price">
                      ¥
                      <span class="money">{{item.aoutPrice?item.aoutPrice.toFixed(2):''}}</span>
                      <span class="num">×1</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="orderInfo">
                <div class="price">共1件商品 总计：¥{{item.oorderMoneyShifu.toFixed(2)}}</div>
              </div>
              <div class="item-bottom">
                <div class="btn" v-if="item.orderStatus  == 2" @click="handle(item,2)">发货</div>
                <!-- <div class="btn" @click="toDetail(item.id)">订单详情</div> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="show">
      <div class="popup">
        <div class="popup-mid">
          <van-cell-group>
            <van-cell title="物流公司" is-link :value="company||'请选择'" @click="companyShow = true" />
            <van-field v-model="number" input-align="right" clearable label="物流单号" placeholder="请输入" @blur="blur()" />
            <van-field :rows="3" v-model="remark" input-align="right" label="详细信息" placeholder="请输入" type="textarea" clearable maxlength="50" show-word-limit @blur="blur()" />
          </van-cell-group>
        </div>
        <div class="popup-bottom">
          <div class="btn" @click="submit">确认发货</div>
        </div>
      </div>
    </van-popup>
    <van-popup v-model="companyShow" position="bottom">
      <van-picker show-toolbar title="物流公司" :columns="companyList" value-key="comName" @cancel="companyShow = false" @confirm="onConfirm" />
    </van-popup>
  </div>
</template>
<script>
import { Tab, Tabs, Dialog, Popup, Picker, CellGroup, Cell, Field } from "vant";
import {
  getActivityOrderManagementList,
  shopHuoOrders,
  getCompanyList
} from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Picker.name]: Picker,
    [Popup.name]: Popup,
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field
  },
  data() {
    return {
      active: 0,
      activeValue: "",
      orderClassifyList: [
        { label: "全部", value: "" },
        { label: "待付款", value: "1" },
        { label: "待发货", value: "2" },
        { label: "待收货", value: "3" },
        { label: "已完成", value: "5" }
      ],
      mescroll: null,
      list: [],
      orderId: "",
      orderNo: "",
      show: false,
      company: "",
      companyId: "",
      number: "",
      remark: "",
      companyShow: false,
      companyList: []
    };
  },
  methods: {
    onClick(name, title) {
      this.activeValue = this.orderClassifyList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    handle(item) {
      this.orderId = item.id;
      this.show = true;
    },
    onConfirm(value, index) {
      this.company = value.comName;
      this.companyId = value.id;
      this.companyShow = false;
    },
    submit() {
      if (!this.companyId) {
        this.$toast("请选择物流公司");
        return;
      }
      if (this.company != '上门自提' && !this.number) {
        this.$toast("请输入物流单号");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      shopHuoOrders({
        orderno: this.orderId,
        hlCompany: this.companyId,
        hlBillno: this.number,
        hlMsg: this.remark
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.show = false;
          this.$toast("已发货");
          this.mescroll.resetUpScroll();
        } else {
          this.show = false;
          this.$toast(res.msg);
        }
      });
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let $this = this;
      let dataObj = {
        pageNum: pageNum,
        pageSize: pageSize,
        orderStatus: this.activeValue
      };
      getActivityOrderManagementList(dataObj)
        .then(res => {
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            $this.$toast.fail(res.msg);
          }
        })
        .catch(res => {
          errorCallback && errorCallback();
          console.log(res);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("list", {
        //在vue的mounted生命周期初始化mescroll,确保此处配置的id能够被找到
        up: {
          callback: this.upCallback, //上拉回调
          //以下参数可删除,不配置
          isBounce: false, //此处禁止ios回弹,解析(务必认真阅读,特别是最后一点): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //可配置每页8条数据,默认10
          empty: {
            //配置列表无任何数据的提示
            warpId: "listData",
            icon: require("@/assets/img/nodata.png"),
            tip: "暂无数据"
            //						  	btntext : "去逛逛 >" ,
            //						  	btnClick : function() {
            //						  		alert("点击了去逛逛按钮");
            //						  	}
          },
          noMoreSize: 10,
          scrollbar: { use: false },
          htmlNodata: '<p class="upwarp-nodata">-- 无更多数据 --</p>',
          lazyLoad: {
            use: true // 是否开启懒加载,默认false
          }
        }
      });
    },
    //上拉回调 page = {num:1, size:10}; num:当前页 ,默认从1开始; size:每页数据条数,默认10
    upCallback: function(page) {
      //联网加载数据
      let $this = this;
      $this.loadData(
        page.num,
        page.size,
        function(curPageData) {
          //curPageData=[]; //打开本行注释,可演示列表无任何数据empty的配置

          //如果是第一页需手动制空列表 (代替clearId和clearEmptyId的配置)
          console.log(curPageData);
          if (page.num == 1) $this.list = [];
          if (curPageData.list) {
            curPageData.list.map(item => {
              $this.list.push(item);
            });
          }
          //联网成功的回调,隐藏下拉刷新和上拉加载的状态;
          //mescroll会根据传的参数,自动判断列表如果无任何数据,则提示空;列表无下一页数据,则提示无更多数据;
          //   console.log("page.num="+page.num+", page.size="+page.size+", curPageData.length="+curPageData.rows.length+", $this.goodsList.length==" + $this.pdlist.length);
          console.log(page);

          //方法二(推荐): 后台接口有返回列表的总数据量 totalSize
          $this.mescroll.endBySize(
            curPageData.list ? curPageData.list.length : 0,
            curPageData.count
          ); //必传参数(当前页的数据个数, 总数据量)
        },
        function() {
          //联网失败的回调,隐藏下拉刷新和上拉加载的状态;
          $this.mescroll.endErr();
        }
      );
    },
    loadCompayList() {
      getCompanyList().then(res => {
        if (res.code == 0) {
          this.companyList = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = "我的订单";
    this.active = this.$route.query.index || 0;
    this.activeValue = this.orderClassifyList[this.active].value;
    this.startMescroll();
    this.loadCompayList();
  }
};
</script>
<style lang="scss" scoped>
.orderManagement {
  .navList {
    margin-bottom: 10px;
  }
  .mid {
    height: calc(100vh - 44px);
    .list {
      // padding-bottom: 40px;
      border-top: 1pt solid #efefef;
      .item {
        padding: 15px 10px;
        border-bottom: 1pt solid #efefef;
        background: #fff;
        .flex {
          display: flex;
          .flex-1 {
            flex: 1;
          }
          .flex-2 {
            flex: 2;
            position: relative;
          }
        }
        .item-top {
          font-size: 15px;
          line-height: 20px;
          .No {
            color: #333;
          }
        }
        .item-goods {
          margin: 15px 0;
          .goods-pic {
            width: 100px;
            height: 100px;
            margin-right: 10px;
            position: relative;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .title {
            font-size: 16px;
            line-height: 25px;
            max-height: 50px;
            text-overflow: -o-ellipsis-lastline;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            line-clamp: 2;
            -webkit-box-orient: vertical;
          }
          .parameter {
            color: #999;
            font-size: 15px;
            line-height: 20px;
            margin: 5px 0 10px;
          }
          .price {
            font-size: 13px;
            font-weight: 600;
            color: #333;
            flex: 2;
            position: absolute;
            width: 100%;
            bottom: 10px;
            .money {
              font-size: 16px;
            }
            .num {
              color: #999;
              margin-left: 10px;
            }
          }
        }
        .orderInfo {
          font-size: 15px;
          .price {
            font-size: 14px;
            text-align: right;
            color: #666;
          }
        }
        .item-bottom {
          text-align: center;
          overflow: hidden;
          padding: 15px 0;
          .qrcode {
            float: right;
            width: 20px;
            height: 20px;
            margin-right: 5px;
            margin-top: 5px;
          }
          .btn {
            width: 80px;
            color: #666;
            border: 1pt solid #e5e5e5;
            float: right;
            height: 30px;
            line-height: 30px;
            border-radius: 5px;
            font-size: 13px;
            margin-left: 5px;
          }
          .btnPay {
            background: #fff;
            color: $base-color;
            border: 1pt solid $base-color;
          }
        }
      }
    }
  }

  .van-popup {
    border-radius: 5px;
  }
  .popup {
    background: #fff;
    width: 85vw;
    height: 65vh;
    position: relative;
    &-top {
      padding: 10px;
      display: flex;
      img {
        width: 70px;
        height: 70px;
        border-right: 5px;
        margin-right: 10px;
      }
      .right {
        flex: 2;
        .name {
          line-height: 20px;
          height: 40px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          text-align: justify;
          word-break: break-all;
        }
        .price {
          margin: 10px 0 0;
          span {
            color: #ee0a24;
            margin-right: 5px;
          }
          del {
            color: #999;
            font-size: 13px;
          }
        }
      }
    }
    &-mid {
      .van-cell {
        padding: 10px;
      }
    }
    &-bottom {
      padding: 10px;
      position: absolute;
      width: calc(100% - 20px);
      height: 45px;
      bottom: 0;
      left: 0;
      .btn {
        width: 100%;
        line-height: 45px;
        text-align: center;
        background: #ee0a24;
        color: #fff;
        font-size: 13px;
        border-radius: 5px;
      }
    }
  }
}
</style>
